
@import "init";


@keyframes accordionOpen {
  from {
    transform-origin: center top;
    transform: rotateX(90deg) translate(0, 0);
    opacity:0;
  }
  to {
    transform: rotateX(0deg) translate(0, 0) perspective(400px);
    opacity:1;
  }
}

@keyframes accordionClose {
  from {
    transform: rotateX(0deg) translate(0, 0) perspective(400px);
    opacity:1;
  }
  to {
    transform-origin: center top;
    transform: rotateX(90deg) translate(0, 0);
    opacity:0;
  }
}

@keyframes simpleFade50 {
  0% {
    opacity: 0;
  }
  100% {
    opacity: .5;
  }
}

@keyframes simpleFade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0;
    transform: rotateX(-45deg);
    transform-origin: center top;
  }
  100% {
    opacity: 1;
    transform: rotateX(0deg);
    transform-origin: left top;
  }
}

@keyframes iscrollDislodgeTrainBehind {
  from {
    right: 93%;
  }
  to {
    right: 93.066%;
  }
}
@keyframes iscrollDislodgeTrainForward {
  from {
    right: 93.066%;
  }
  to {
    right: 93%;
  }
}

@keyframes dislodgeTrainBehind {
  from {
    right: 50%;
  }
  to {
    right: 50.166%;
  }
}
@keyframes dislodgeTrainForward {
  from {
    right: 50.166%;
  }
  to {
    right: 50%;
  }
}

@keyframes fixPager {
  from {
    position: fixed;
    height: 0;
  }
  to {
    height: 44px;
  }
}
@keyframes unfixPager {
  from {
    position: fixed !important;
    top: 75px;
    left: 0;
    right: 0;
    z-index: 2;
    padding: 0 20px;
    height: 44px;
    background: red;
  }
  to {
    position: fixed !important;
    top: 75px;
    left: 0;
    right: 0;
    z-index: 2;
    padding: 0 20px;
    height: 44px;
    background: red;
  }
}
