
@import "init";
@import "variables";
@import "animation";
@import "mixins";

p {
  margin-bottom: 40px;
}
h1.title {
  font-family: $pathway_font_stack;
  font-weight: normal;
  font-size: 36px;
}

body {
  background:$body_bg_color;
  color:$body_color;
  font-family: $museo_font_stack;
  font-size:20px;
}

a {
  color: $lastcall_red;
  text-decoration: none;
}

blockquote {
  background: $body_color;
  border: 3px solid $body_color;
  color: #fff;
  font-size: 14px;
  font-family: $museo_font_stack;
  font-weight: 300;
  box-shadow: inset 0 0 0 1px #fff;
}

.title,
.field--name-title {
  text-transform: uppercase;
}
.visually-hidden {
  margin:0;
}

/**
 * Form styles:
 */
textarea {
  border-width:0;
}
input[type="text"], input[type="password"], input[type="email"] {
  border-width: 0;
}
.dropdown ul {
  padding-left:0;
  margin-top:0;
  margin-bottom:0;
}
input[type="submit"], button {
  font-size:26px;
  text-transform: uppercase;
  text-decoration: none;
  display: inline-block;
  padding:0 45px;
  line-height: 55px;
}

input[type="submit"], button, a.button {
  background-color:transparent;
  border-radius: 5px;
  color:$body_color;
  border:3px solid $lastcall_grey;
  font-family: $pathway_font_stack;
  font-weight: normal;
  transition-duration: 0.3s;
  transition-property: color, border, background-color;

  &:hover {
    background: $lastcall_grey;
    color: #fff !important;
    border: 3px solid $lastcall_grey;
  }
}

a {
  &.nostyle {
    color:inherit;
    text-decoration: inherit;
  }
  &.button {
    @extend button;
  }
}

.page-header {
  $nav_height:120px;
  background:white;
  width:100%;
  border-bottom:5px solid $lastcall_grey;
  .menu {
      text-align: center;

    a {
      font-family: $pathway_font_stack;
      font-weight: normal;
      vertical-align: middle;
      font-size:26px;
      text-decoration: none;
      line-height:40px;
      text-transform: uppercase;
      color:$body_color;
    }
    li {
      margin: 8px 0;
      &.home {
        h1 {
          // regular li height - image height / 2 = negative top and bottom margin
          margin:((40px - 105px) / 2) 0;
          @include background-image-retina('#{$img_path}/nav_logo', 'png', 98px, 105px);
          text-indent:-999em;
          height:105px;
          width:98px;
          position:relative;
          z-index: 1;
        }
        a {
          padding:0;
        }
      }
    }
  }
  #active-indicator {
    border-bottom:5px solid #3F444F;
    height:1px;
    position: absolute;
    bottom:0;
  }
}

#pane-contact {
  .block {
    max-width:835px;
    margin-left:auto;
    margin-right:auto;
  }
  #contact-message-feedback-form {
    float: left;
    width: 70%;
  }
  ul.leadin {
    float:left;
    max-width: 25%;
    padding-left: 3%;
  }
  h4 {
    text-align: center;
    text-transform: uppercase;
    font-family: $pathway_font_stack;
    font-weight: normal;
    font-size: 24px;
    margin-left: -10px;
    margin-bottom: 20px;
    span {
      display: inline-block;
      padding:0 15px;
      position: relative;
    }

    hr {
      background: $body_color;
      border: 0;
      display: block;
      margin: 0;
      position: absolute;
      top:50%;
      height:1px;
    }
  }
