//    @include list-no-style;
//    text-align: center;
//
//    li {
//      word-break: break-word;
//      text-align: center;
//    }
//    span {
//      font-size:24px;
//      font-family:$bello_font_stack;
//      line-height:1em;
//    }
//    .icon {
//      display: block;
//      margin-bottom:12px;
//      color:$body_color;
//      &:before {
//        font-size:60px;
//        border:8px solid;
//        border-radius:3em;
//        padding:20px;
//        margin:0;
//      }
//    }
//  }

  input[type="email"], select, textarea, .dropdown {
    width:100%;
  }
  textarea {
    height:180px;
  }
  .form-actions {
    text-align: right;
  }
  .dropdown {
    &, & li, .selected {
      background: #3F444F;
      color:white;
    }
    li {
      border-top:1px solid white;
      font-family: $pathway_font_stack;
      font-weight: normal;
      text-transform: uppercase;
      font-size:28px;
    }
    &.focus {
      z-index: auto;
      &:after {
        background: transparent;
      }
    }
    .selected:after {
      border-left:1px solid white;
      width:36px;
      box-shadow: none;
    }
    div {
      transition:none;
      background: transparent;
      opacity:1;
    }
    &.open {
      position: relative;
      z-index: auto;
      div {
        z-index: auto;
