.js {
  .js-hidden {
    display: none;
  }
}

.ui-dialog {
  border-radius: 0;
  box-shadow: $body_color 0 0 0 5px, $body_color 0 0 10px 3px;

  .ui-dialog-titlebar {
    background: none;
    border: none;
    height: 0;
    padding: 0;
  }

  .ui-dialog-title {
    display: none;
  }

  .ui-dialog-buttonpane {
    position: absolute;
    top: -67px;
    left: -27px;
    background: none;
    border-top: none;
  }

  button {
    &.ui-button.ui-widget.ui-state-default {
      background-color: transparent;
      &:focus {outline:0;}
      border: none;
      &.previous-node {
        @include background-image-retina("#{$img_path}/src/overlay_right", "png", 15px, 29px);
        height:29px;
        width:15px;

        &:hover {
          @include background-image-retina("#{$img_path}/src/overlay_right_hover", "png", 15px, 29px);
        }
      }
      &.next-node {
        @include background-image-retina("#{$img_path}/src/overlay_left", "png", 15px, 29px);
        height:29px;
        width:15px;

        &:hover {
          @include background-image-retina("#{$img_path}/src/overlay_left_hover", "png", 15px, 29px);
        }
      }
      &.ui-dialog-titlebar-close {
        @include background-image-retina("#{$img_path}/src/overlay_close", "png", 29px, 29px);
        height:29px;
        width:29px;
        position: absolute;
        right: -5px;
        top: -40px;

        &:hover {
          @include background-image-retina("#{$img_path}/src/overlay_close_hover", "png", 29px, 29px)
        }
        span {
          display: none;
        }
      }
      .ui-button-text {
        text-indent: -999px;
      }
    }
  }
}

// Blog
#block-views-block-blog-latest-posts-block-1,
#block-views-block-blog-view-a-topic-block-1 {
  overflow: hidden;
  h2 {
    font-family: $pathway_font_stack;
    font-weight: normal;
    margin: 10px;
  }
  .view-content {
    overflow: hidden;
  }
}

.view-blog-latest-posts, .view-blog-view-a-topic {
  $slot_height: 25px;
  .slot-links-container {
    @include slot-links-container($slot_height);
  }
  @include slot-link($slot_height, '.more-link', '.slot-link--normal', '.slot-link--hover');
  .slot-link--normal, .slot-link--hover {
    font-family: $pathway_font_stack;
    &::after {
      content:      '\e80F';
      font-family:  'fontello';
      font-size:    26px;
      padding-left: 5px;
      position:     relative;
      top:          2px;
    }
  }
  .slot-link--hover {
    color: $lastcall_red;
  }
}

#block-views-block-blog-latest-posts-block-1 {
  h2 {
    text-transform: uppercase;
  }
}

#block-views-block-blog-view-a-topic-block-1 {
  clear: left;
  h2 {
    font-size: 20px;
  }
  form.views-exposed-form {
    border-bottom: 1px solid $body_color;
    margin: 10px 20px 10px 10px;
    .form-item {
      margin: 0;
    }
    ul.views-exposed-filter-tabs-processed {
      list-style: none;
      margin: 0;
      padding: 0;
      li.tab {
        cursor: pointer;
        margin: 0 10px 0 0;
        padding: 0 10px;
        background-color: $body_color;
        color: white;
        float: left;
        font-size: 14px;
        line-height: 25px;
        text-align: center;
        text-decoration: none;
        text-transform: uppercase;
        &.active {
          border-top: 1px solid $body_color;
          border-right: 1px solid $body_color;
          border-left: 1px solid $body_color;
          background-color: $body_bg_color;
          color: $body_color;
          margin-bottom: -1px;
        }
      }
    }
    .form-actions {
      display: none;
    }
  }
}

article.node--type-blog {
  &.node--view-mode-frontpage {
    position: relative;
    overflow: hidden;
    > a {
      display: block;
      position: absolute;
      width: 100%;
      height: 100%;
      z-index: 10;
    }
    .field--name-field-blog-image {
      img {
        width: 100%;
        height: auto;
        display: block;
      }
    }
    .blog-summary {
      background-color: #000;
      background-color: rgba(0, 0, 0, 0.6);
      display: block;
      position: absolute;
      bottom: 0;
      width: 100%;
      h2 {
        color: white;
        font-family: $pathway_font_stack;
        font-weight: normal;
        font-size: 25px;
        margin: 10px;
      }
      .blog-date-link {
        color: white;
        display: block;
        font-size: 12px;
        font-weight: 300;
        margin: 0 10px -1px;
        border-top: 1px solid white;
        overflow: hidden;
        div {
          padding: 10px 0;
          &.blog-date {
            float: left;
          }
          &.blog-link {
            float: right;
            i {
              padding-left: 5px;
            }
          }
        }
      }
    }
  }
}

// About
#pane-about {
  .view-header {
    a {
      &:hover {
        text-decoration: underline;
      }
    }
  }
}

.profile {
  &.compact {
    a {
      font-family: $museo_font_stack;
      font-weight: bold;
      color: $body_color;
      font-size: 12px;
      &:hover {
        color: $lastcall_red;
        text-decoration: none;
      }
      i {
        font-size: 20px;
      }
    }

    #block-views-block-employees-staff-block & {
      &:hover {
        .field--name-field-job-title {
          p {
            text-align: left;
          }
        }
      }
    }

    .field--type-text-with-summary,
    .field--type-text,
    .field--type-link {
      display: none;
      clear: both;
      width: 100%;
      text-align: left;
      font-size: 14px;
    }

    .field--name-user-picture {
      &:before {
        border-radius: 50%;
        background: $body_color;
        width: 220px;
        height: 220px;
        content: ' ';
        display: block;
        top: 19px;
      }
      &:after {
        border-radius: 50%;
        background: rgba(0, 0, 0, .5);
        width: 220px;
        height: 220px;
        content: ' ';
        display: block;
        position: absolute;
        top: 20px;
        transition-property: margin-top;
        transition-duration: 0.5s;
      }

      img {
        border-radius: 50%;
        position: relative;
        margin: 0 auto;
        display: block;
        margin-top: 9px;
        margin-left: -1px;
        padding: 0 1px;
        transition-property: box-shadow;
        transition-duration: 0.5s;
        filter: grayscale(100%);
      }
    }

    .field--name-field-full-name {
      font-family: $pathway_font_stack;
      font-weight: normal;
      font-size: 24px;
      text-transform: uppercase;
      display: block;
      color: #fff;
      &:after {
        display: block;
        width: 140px;
        margin: 5px auto;
        height: 1px;
        background: #fff;
        content: ' ';
      }
    }

    .field--name-field-job-title {
      font-family: $museo_font_stack;
      color: #fff;
      display: block;
      text-align: center;
      width: 220px;
      font-size: 14px;
      #block-views-block-employees-staff-block & {
        p {
          text-align: center;
        }
      }
    }

    .views-row:last-child & {
      img {
        filter: grayscale(0);
      }
      .field--name-user-picture:after {
        background:none;
      }
      .field--name-field-full-name {
        font-size: 40px;
        top: 68px;
        transition: top 0.5s;
        &:after {
          margin: 10px auto;
        }
      }
      .field--name-field-job-title {
        top: 139px;
        transition: top 0.5s;
        width:80px;
        margin:0 70px;
      }
      &:hover {
        box-shadow: none;
        background: none;
        border: 0;
        .field--name-field-full-name {
          color: white;
          display: block;
          padding-top: 0;
          position: absolute;
          text-align: center;
          width: 220px;
          &:after {
            border-top: 0;
            display: block;
            float: none;
            width: 140px;
            margin: 10px auto;
            height: 1px;
            background: white;
            content: ' ';
          }
          transition: top 0.5s;
        }
        .field--name-field-job-title {
          color: white;
          font-style: normal;
          left: inherit;
          padding-top: 0;
          position: absolute;
          text-align: center;
          transition: top 0.5s;
        }
        .field--name-field-email {
          display: none;
        }
      }
    }
  }
}

