#pane-work {
  $slot_height: 30px;
  .pager {
    @include slot-links-container($slot_height);
    margin-top:     35px;
    padding-bottom: 5px;
  }
  .pager__items {
    margin: 0;
  }
  .pager__item--first, .pager__item--previous, .pager__item--next, .pager__item--last {
    @include slot-link($slot_height);
  }
  .pager__item--previous, .pager__item--last {
    a {
      &::after, &::before {
        content: '\e810';
      }
    }
  }
  .pager__item--next, .pager__item--first {
    a {
      &::after, &::before {
        content: '\e80F';
      }
    }
  }
  #block-views-block-portfolio-block-1 {        // For specificity.
    .pager__item {
      padding-top: 0;
    }
  }
}

